<template>
  <div class="additem">
    <v-form v-model="valid">
        <v-text-field
          label="Name"
          v-model="name"
          :rules="nameRules"
          :counter="10"
          required
        ></v-text-field>
        <v-text-field
          label="E-mail"
          v-model="email"
          :rules="emailRules"
          required
        ></v-text-field>

        <v-select
            label="Item"
            v-model="select"
            :items="items"
            required
          ></v-select>

    </v-form>      
  </div>
</template>

<script>
export default {
  name: 'Additem',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      name: 'asdasd',
      email: 'Asdasd',
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4'
      ],
      
     
    }
  },
   created () {
      console.log(this.$route.path);
   }
}
</script>



